@use 'sass:map';
@use '../abstracts/variables';
@use '../abstracts/functions';
@use '../abstracts/mixins';
@use '../abstracts/placeholders';





/*common override n only library*/
.title_wrap {
	.title_heading {
		@include mixins.justify_width(center);
		.fc_light_gray {
			color: variables.$textColorLightGray;
		}
		.gap {
			margin: 0 12px;
			font-weight: variables.$fontWeightRegular;
		}
	}
}
.form_chk {
	.check_count {
		@include mixins.font_size_xxs();
		font-weight: variables.$fontWeightRegular;
		color: variables.$textColorLightGray;
		margin-left: 12px;
		.fw_bold {
			color: variables.$textColorBlack;
		}
	}
}

.tab_wrap {
	&.my_library {
		> .tab_list_wrap {
			
			> .tabs {
				@include mixins.justify_width(start, start);
				flex-wrap: nowrap;
				
				
				.tab_item {
					//flex-grow: 2;
					flex-shrink: 0;
					padding: 0 8px;
					&:first-child {
						padding-left: 0;
					}
					&:last-child {
						padding-right: 0;
					}
				}
			}
		}
	}//my_library
	&.my_library_prod_detail {
		margin-top: 50px;
		
		.tab_content {
			.no_data.has_desc_lg {
				.no_data_desc_lg {
					&:before {
						@include mixins.ratio_images('../images/ico_nodata_library', 'png', 40px, 40px);
					}
				}
			}
		}
	
	}
}



.library_induce_comment_box {
	width: auto;
	@include mixins.justify_width(center, start, row, true);
	height: 42px;
	border-radius: 21px;
	padding: 0 24px 0 20px;
	box-sizing: border-box;
	
	
	.comment_text {
		@include mixins.font_size_xxs;
		font-weight: variables.$fontWeightBold;
	}
	
	&.type_before,
	&.type_reading {
		background-color:  rgba(variables.$kyoboGreen, .1);
		.comment_text {
			color: #3C9A17;
		}
	}
	&.type_done {
		background-color:  rgba(variables.$kyoboPurple, .1);
		.comment_text {
			color: #474C98;
		}
	}
	&.type_stop {
		background-color:  variables.$colorGray05;
		.comment_text {
			color: variables.$textColorLightGray;
		}
	}
	
	// 아이콘
	[class^="ico_library_state"] {
		display: inline-block;
		vertical-align: top;
		margin-right: 8px;
		//@include mixins.transition(background 0.2s ease-out);
		background: {
			position: center;
			repeat: no-repeat;
		}
	}
	
	.ico_library_state_before { width: 23px; height: 22px; @include mixins.ratio_images('../images/ico_library_state_before', 'png', 23px, 22px); }
	.ico_library_state_reading { width: 22px; height: 17px; @include mixins.ratio_images('../images/ico_library_state_reading', 'png', 22px, 17px); }
	.ico_library_state_done { width: 17px; height: 22px; @include mixins.ratio_images('../images/ico_library_state_done', 'png', 17px, 22px); }
	.ico_library_state_stop { width: 16px; height: 20px; @include mixins.ratio_images('../images/ico_library_state_stop', 'png', 16px, 20px); }
	
}

.library_history_list {
	display: block;
	
	.library_history_item {
		@include mixins.justify_width();
		position: relative;
		padding: 0 0 10px 16px;
		text-align: left;
		box-sizing: border-box;
		font-size: 0;
		
		&:before {
			content: '';
			display: inline-block;
			position: absolute;
			left: 0;
			top: 40px;
			width: 7px;
			height: 7px;
			box-sizing: border-box;
			border-radius: 50%;
			background-color: #fff;
			border: solid 2px variables.$colorGray01;
			z-index: 1;
		}
		
		&:after {
			content: '';
			display: inline-block;
			position: absolute;
			left: 3px;
			top: 2px;
			bottom: 0;
			width: 1px;
			border-left: 1px dashed variables.$colorGray02;
		}
		
		&:first-child {
			&:after {
				top: 50px;
			}
		}
		
		&:last-child {
			padding-bottom: 0;
			
			&:after {
				height: 40px;
			}
		}
		
		.library_induce_comment_box {
			width: 331px;
			height: 78px;
			border-radius: 10px;
			padding: 0 24px 0 20px;
			
			
			&.type_before,
			&.type_reading {
				border: solid 1px rgba(variables.$kyoboGreen, .3);
			}
			&.type_done {
				border: solid 1px rgba(variables.$kyoboPurple, .3);
				@include mixins.font_size_xs;
				.comment_title {
					font-weight: variables.$fontWeightBold;
					color: #474C98;
					+.comment_text {
						margin-left: 12px;
					}
				}
				.comment_text {
					font-weight: variables.$fontWeightRegular;
					opacity: .8;
				}
			}
			&.type_stop {
				border: solid 1px variables.$colorGray04;
				.comment_text {
					color: variables.$textColorBlack;
					font-weight: variables.$fontWeightRegular;
				}
			}
			
			// 아이콘
			[class^="ico_library_state"] {
				width: 42px;
				height: 42px;
				background-color: #fff;
				border-radius: 21px;
				margin-right: 12px;
			}
			
			
		}//library_induce_comment_box
		
		.library_induce_period_box {
			@include mixins.justify_width(center);
			flex: 1;
			height: 78px;
			border: 1px solid variables.$colorGray04;
			border-radius: 10px;
			padding: 0 24px 0 20px;
			margin-left: 10px;
			font-size: 0;
			box-sizing: border-box;
			
			.period_box {
				.date_item {
					.ico_calendar {
						display: inline-block;
						width: 20px;
						height: 20px;
						margin-right: 6px;
						vertical-align: top;
						background: {
							repeat: no-repeat;
						};
						@include mixins.ratio_images('../images/ico_calendar_lg', 'png', 20px, 20px);
					}
					.text {
						@include mixins.font_size_xs;
						font-weight: variables.$fontWeightMedium;
					}
				}
				.gap {
					@include mixins.font_size_xs;
					font-weight: variables.$fontWeightRegular;
					margin: 0 12px;
				}
			}
			
			.comment_box {
				@include mixins.justify_width(center, end, row, false );
				margin-left: auto;
				.comment_text {
					@include mixins.font_size_xxs;
					font-weight: variables.$fontWeightRegular;
					color: variables.$textColorGray;
					
					
					+.btn_sm {
						margin-left: 12px;
					}
				}
			}
			
		}//library_induce_period_box
	}
}



