		padding-bottom: 40px;
		border-bottom: solid 1px variables.$colorGray03;
		>.prod_item {
			width: calc(100% / 2 - 12px) ;
			margin-left: 24px;
			vertical-align: top;
			border: solid 1px variables.$colorGray04;
			box-sizing: border-box;
			border-radius: 16px;
			
			&:nth-child(2n+1) {
