			}
			
			.prod_area.horizontal {
				@include mixins.justify_width();
				width: auto;
				padding: 29px;
				
				.form_chk {
					//align-self: flex-start;
					flex-shrink: 0;
					margin-right: 10px;
				}
				
				
				.prod_thumb_box {
					
					+.prod_info_box {
						@include mixins.justify_width(start, start, column);
						margin-top: auto;
						height: 203px;
						
						.prod_info {
							height: auto;
						}
						
						.prod_author {
							align-self: auto;
						}
						
					}
					
					&.type_square_round,
					&.type_culture,
					&.type_package {
						+.prod_info_box {
							height: auto;
							margin-bottom: auto;
						}
					}
					
					
					/*&.type_package {
						+.prod_info_box {
							.prod_name {
								height: calc(#{variables.$fontSizeSmLineHeight} * 3);
								@include mixins.multi_ellipsis(variables.$fontSizeSmLineHeight, 3, false);
							}
						}
					}*/
					
				}
				
				.right_area{
					flex-shrink: 0;
					width: 30px;
					margin-left: auto;
					padding-left: 16px;
					
				}
			}
			
			.library_package_prod_wrap {
				padding: 24px 0 30px;
				border-top: solid 1px variables.$colorGray04;
				
				.prod_swiper_wrap {
					.prod_list {
						
						.prod_item {
							margin-right: 8px;
							&:first-child {
								margin-left: 30px;
							}
							&:last-child {
								margin-right: 30px;
							}
							.prod_area {
								width: 64px;
								
								.prod_thumb_box {
									&.size_xxs {
										//width: 64px;
									}
								}
							}
						}
					}
					.swiper-scrollbar {
						left: 30px;
						right: 30px;
						width: auto;
					}
					
				}//prod_swiper_wrap
			}
			
			.library_footer {
				//justify-content: space-around;
				padding: 18px 29px;
				border-radius: 0 0 16px 16px;
				background: variables.$colorGray06;
				
				.library_func_list {
					@include mixins.justify_width(center, between);
					.library_func_item {
						position: relative;
						flex: 1;
						text-align: center;
						
						+.library_func_item {
							padding-left: 1px;
							
							&:before {
								content: '';
								position: absolute;
								left: 0;
								display: inline-block;
								font-size: 0;
								width: 1px;
								height: 10px;
								background-color: variables.$colorGray03;
