				width: 92px;
				height: 92px;
				@include mixins.transform(translate(-50%, -50%));
				
				&:after {
					display: block;
					content: "";
					width: 92px;
					height: 92px;
					background: {
						repeat: no-repeat;
						position: center;
					}
