				}
			}
			.right_area {
				margin-left: auto;
			}
		}
		
	}
}

//리스트 상세 > 코멘트
.library_comment_list {
	position: relative;
	margin: 0 -12px;
	padding-bottom: 40px;
	
	&:after {
		content: '';
		position: absolute;
		left: 12px;
		right: 12px;
		bottom: 0;
		height: 1px;
		background-color: variables.$colorGray04;
	}
	
	.library_comment_item {
		width: 50%;
		border: 0;
		padding: 0 12px;
		margin-left: 0;
		box-sizing: border-box;
		
		&:nth-child(n+3) {
			padding-top: 24px;
		}
		
		.library_comment_area {
			border: solid 1px variables.$colorGray04;
			box-sizing: border-box;
			border-radius: 16px;
			
			
			.library_comment_body {
				box-sizing: border-box;
				background-color: variables.$colorGray06;
				border-radius: 16px 16px 0 0;
				padding: 40px 30px;
				
				&:before {
					content: '';
					display: block;
					width: 30px;
					height: 21px;
					margin: 0 auto 20px;
					background: {
						repeat: no-repeat;
						position: center center;
					}
					@include mixins.ratio_images('../images/ico_marker_left_30x21', 'png', 30px, 21px);
				}
				
				.comment_text {
					position: relative;
					display: block;
					text-align: center;
					@include mixins.font_size_xs;
				}
				
			}
			.library_comment_info {
				@include mixins.justify_width(center);
				padding: 20px 30px;
				
				.left_area {
					.date {
						@include mixins.font_size_xxs;
						color: variables.$textColorLightGray;
						font-weight: variables.$fontWeightMedium;
					}
				}
				.right_area {
					margin-left: auto;
					@include mixins.justify_width(center);
					
					.btn_text_link {
						@include mixins.font_size_xxs;
						.text {
							text-decoration: none;
						}
					}
					
					.gap {
						position: relative;
						display: inline-block;
						font-size: 0;
						width: 1px;
						height: 9px;
						background-color: variables.$colorGray03;
						margin: 0 8px 0;
						vertical-align: top;
						box-sizing: border-box;
					}
					
				}
			}//library_comment_info
			.library_comment_prod_info {
				@include mixins.justify_width(center);
				border-top: solid 1px variables.$colorGray04;
				padding: 20px 0;
				margin: 0 30px;
				.prod_area {
					.prod_thumb_box {
						width: 46px;
						
						&.type_square_round {
							.prod_link {
								.img_box {
									border-radius: 6px;
									&:before {
										border-radius: 6px;
									}
								}
							}
						}
						&.type_bookcover {
							.prod_link {
								.img_box {
									border-radius: 6px 6px 6px 0;
									&:before {
										border-radius: 6px 6px 6px 0;
									}
								}
							}
						}
					}
					
					.prod_info_box {
						.prod_info {
							@include mixins.justify_width(center);
							border-radius: 20px;
							height: 40px;
							border: solid 1px variables.$colorGray04;
							background-color: variables.$colorGray06;
							padding: 0 20px;
						}
						
					}
				}
				
			}//library_comment_prod_info
			
		}
	}//library_comment_item
}//library_comment_list


//리스트 상세 > 문장수집
.killing_part_list {
	&.library {
		
		ul {
			font-size: 0;
			li {
				display: inline-block;
				width: calc(100% / 2 - 12px);
				margin-left: 24px;
				vertical-align: top;
				box-sizing: border-box;
				
				&:nth-child(2n+1) {
					margin-left: 0;
				}
				
				&:nth-child(n+3) {
					margin-top: 24px;
				}
			}
		}
		
		.killing_part_contents {
			box-sizing: border-box;
			
			.killing_part_bg {
				height: 397px;
				
				.like_count {
					display: inline-block;
					vertical-align: top;
					
					.ico_heart_white {
						display: inline-block;
						width: 20px;
						height: 20px;
						margin-right: 4px;
						background: {
							position: center;
							repeat: no-repeat;
						}
						@include mixins.ratio_images('../images/ico_heart_white_h20', 'png', 20px, 20px);
						@include mixins.transition(background 0.2s ease-out);
					}
					
					.text {
						display: inline-block;
						position: relative;
						@include mixins.font_size_xxs;
						vertical-align: top;
						padding-top: 1px;
						color: #fff;
					}
				}
				
				.slide_inner_middle {
					
					.text_killing_part {
						text-align: center;
						
						padding: 0 40px;
					}
				}
				
				
			}
			
			.library_info_box {
				@include mixins.justify_width(center);
				border-top: solid 1px variables.$colorGray04;
				padding: 20px 30px;
				
				.left_area {
					.date {
						@include mixins.font_size_xxs;
						color: variables.$textColorLightGray;
						font-weight: variables.$fontWeightMedium;
					}
				}
				.right_area {
					margin-left: auto;
					@include mixins.justify_width(center);
					
					.btn_text_link {
						@include mixins.font_size_xxs;
						.text {
							text-decoration: none;
						}
					}
					
					.gap {
						position: relative;
						display: inline-block;
						font-size: 0;
						width: 1px;
						height: 9px;
						background-color: variables.$colorGray03;
						margin: 0 8px 0;
						vertical-align: top;
						box-sizing: border-box;
					}
					
				}
			}
		}
	}
}
