@use 'sass:map';
@use '../abstracts/variables';
@use '../abstracts/functions';
@use '../abstracts/mixins';
@use '../abstracts/placeholders';

// 프로필 공통
.profile_thumb_box{
	position: relative;
	width: 92px;
	height: 92px;

	.thumb_box{
		width: 100%;
		height: 100%;
		border-radius: 100%;
		background: {
			size: cover;
			repeat: no-repeat;
		};

		img{
			visibility: hidden;
			position: absolute;
		}
	}

	.badge_lg{
		position: absolute;
		top: 0;
		right: -23px;
	}
}

// 공통으로 적용? 테이블 내부만 적용? 정의 후 정리 필요
.tbl_row_wrap {
	td {
		.profile_thumb_box {
			& + .btn_wrap {
				margin-top: 16px;
				text-align: left;
			}
		}
	}
}

// lnb영역 프로필
.my_profile_area{
	position: relative;
	overflow: hidden;
	margin-bottom: 30px;
	padding: 24px;
	border-radius: 16px;

	.blur_img_box{
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;

		&:before{
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background-color: rgba(variables.$textColorBlack, .1);
			z-index: 1;
		}
	}

	&:before{
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: rgba(variables.$kyoboPurple, .8);
		z-index: 1;
	}

	.profile_inner{
		position: relative;
		z-index: 2;
	}

	.profile_thumb_box{
		position: relative;
		width: 60px;
		height: 60px;
	}

	.profile_name_box{
		height: 56px;
		margin-top: 10px;

		.name{
			@include mixins.font_size_md;
			font-weight: variables.$fontWeightBold;
			color: #fff;
		}

		.btn_setting{
			@include mixins.transform(translateY(1px));
		}
	}

	.profile_info_list{
		margin-top: 12px;
		font-size: 0;

		.info_item{
			display: inline-block;
			vertical-align: top;

			~ .info_item{
				&:before{
					content: '';
					display: inline-block;
					width: 1px;
					height: 24px;
					margin: 10px 15px 0;
					background-color: #fff;
					border-radius: 10px;
					opacity: .1;
					vertical-align: top;
				}
			}
		}

		.info_link{
			display: inline-block;
			vertical-align: top;

			.val{
				display: block;
				font-size: 17px;
				line-height: 20px;
				font-weight: variables.$fontWeightBold;
				letter-spacing: variables.$letterSpacing;
				color: #fff;
			}

			.title{
				display: block;
				margin-top: 2px;
				@include mixins.font_size_min;
				color: #fff;
				opacity: .8;
			}
		}
	}
}

/* 바로드림 관련 공통 사용 텍스트 */
.pickup_info_text {
	display: block;
	font-size: 0;

	.info_item {
		display: inline-block;
		@include mixins.font_size_sm;
		color: variables.$textColorBlack;
		font-weight: variables.$fontWeightMedium;
		vertical-align: top;
	}
	.gap {
		display: inline-block;
		font-size: 0;
		width: 1px;
		height: 10px;
		background-color: variables.$colorGray03;
		margin: 6px 12px 0 12px;
		vertical-align: top;
		box-sizing: border-box;
	}
}

/* 바로가기 서비스 리스트 */
.favorite_svc_list {
	display: block;
	font-size: 0;

	.favorite_svc_item {
		display: inline-block;
		width: calc((100% - 10px) / 2);
		padding: 15px 24px;
		margin-left: 10px;
		border: 1px solid variables.$colorGray04;
		border-radius: 10px;
		box-sizing: border-box;
		vertical-align: top;

		.btn_favorite {
			margin-top: 2px;
		}

		&:nth-child(2n+1) {
			margin-left: 0;
		}

		&:nth-child(n+3) {
			margin-top: 10px;
		}
	}
}

/* 주문/배송 조회 상세 박스 */
.order_summary_box {
	display: block;
	background-color: #fff;
	border: 1px solid variables.$colorGray04;
	border-radius: 16px;
	overflow: hidden;
	word-break: break-all;

	.box_header {
		@include mixins.justify_width(center);
		min-height: 60px;
		padding: 15px 24px;
		background-color: rgba(variables.$kyoboPurple, 0.06);
		box-sizing: border-box;

		.label {
			display: inline-block;
			font-size: 0;
			vertical-align: top;

			.gap {
				display: inline-block;
				font-size: 0;
				width: 1px;
				height: 10px;
				background-color: variables.$colorGray03;
				margin: 6px 10px 0 10px;
				vertical-align: top;
				box-sizing: border-box;
			}

			.order_date, .order_num {
				display: inline-block;
				@include mixins.font_size_def;
				color: variables.$textColorBlack;
				vertical-align: top;
			}
			.order_date {
				@include mixins.font_size_def;
				font-weight: variables.$fontWeightBold;
			}
			.order_num {
				@include mixins.font_size_xs;
				font-weight: variables.$fontWeightMedium;

				.num {
					margin-left: 2px;
					@include mixins.font_size_def;
				}
			}
		}

		.right_box{
			flex-shrink: 0;
			margin-left: auto;
			padding-left: 20px;
			box-sizing: border-box;
		}

		.tbl_row_wrap {
			margin: 3px 0;

			td {
				.date_picker_box {
					width: 542px;
				}
			}
		}
	}
	.box_body {
		display: block;
		padding: 24px 24px;

		// 주문결제 상태 리스트
		.order_summary_list {
			.order_summary_item {
				display: block;
				@include mixins.clearfix;

				& ~ .order_summary_item {
					margin-top: 8px;
				}

				.label {
					width: 80px;
					float: left;
					padding-right: 10px;
					@include mixins.font_size_xs;
					color: variables.$textColorLightGray;
					font-weight: variables.$fontWeightMedium;
					box-sizing: border-box;
				}

				.right_box {
					float: left;
					@include mixins.font_size_xs;
					color: variables.$textColorBlack;

					.price {
						display: inline-block;
						font-size: 0;
						vertical-align: top;

						.val, .unit {
							@include mixins.font_size_xs;
							color: variables.$textColorBlack;
							vertical-align: top;
						}

						.val {
							font-weight: variables.$fontWeightBold;
						}
					}

					.bank_account {
						display: inline-block;
						vertical-align: top;
						text-decoration: underline;

						& + .btn_more_view {
							margin-top: 1px;
							margin-left: 4px;
						}
					}
				}
			}

			& + .valid_check {
				margin-top: 16px;
			}
		}

		// 바로드림 상태
		.pickup_store_area {
			@include mixins.justify_width(center);

			.pickup_status {
				display: inline-block;
				position: relative;
				padding-left: 16px;
				@include mixins.font_size_xs;
				color: variables.$textColorGreen;
				font-weight: variables.$fontWeightMedium;
				vertical-align: top;

				&:before {
					content: '';
					display: block;
					position: absolute;
					left: 0;
					top: 6px;
					width: 12px;
					height: 8px;
					background: {
						repeat: no-repeat;
					};
					@include mixins.ratio_images('../images/ico_pickup_status', 'png', 12px, 8px);
				}
			}

			.right_box {
				flex-shrink: 0;
				margin-left: auto;
				padding-left: 20px;
				box-sizing: border-box;
			}
		}
	}

	// 바로드림
	&.type_green {
		.box_header {
			background-color: rgba(variables.$kyoboGreen, 0.06);
		}
	}

	& + .info_text_box {
		margin-top: 20px;
	}

	& + .tbl_prod_wrap {
		margin-top: 30px;
	}

	& + .tbl_col_wrap {
		margin-top: 30px;
	}

	& + .no_data {
		margin-top: 30px;
	}
}

// 팝업 내부
.dialog_contents {
	.order_summary_box {
		border-radius: 10px;
	}
}

/* 주문/배송 조회 상세 > 결제정보 리스트 */
.payment_info_wrap {
	@include mixins.justify_width;
	align-items: stretch;
	padding: 30px 10px;
	border-top: 1px solid #000;
	border-bottom: 1px solid #eaeaea;
	box-sizing: border-box;

	.payment_info_area {
		display: block;
		position: relative;
		flex: 1 0;
		box-sizing: border-box;

		& ~ .payment_info_area {
			margin-left: 30px;
			padding-left: 30px;

			&:before {
				content: '';
				display: block;
				position: absolute;
				left: 0;
				top: 6px;
				width: 1px;
				height: calc(100% - 12px);
				border-left: 1px dashed variables.$colorGray04;
			}
			//border-left: 1px dashed variables.$colorGray04;
		}
	}

	.payment_total_item {
		display: block;

		& ~ .payment_total_item {
			margin-top: 20px;
		}

		.label {
			display: inline-block;
			position: relative;
			vertical-align: top;
			@include mixins.font_size_sm;
			font-weight: variables.$fontWeightMedium;

			&.type_link {
				padding-right: 11px;

				&:after {
					content: '';
					display: block;
					position: absolute;
					top: 6px;
					right: 0;
					width: 5px;
					height: 9px;
					background: {
						repeat: no-repeat;
					};
					@include mixins.ratio_images('../images/btn_more_view_black', 'png', 5px, 9px);
				}

				&:hover, &:focus, &:active {
					text-decoration: underline;
				}
			}
		}

		.price {
			display: inline-block;
			font-size: 0;
			vertical-align: top;

			.val, .unit {
				display: inline-block;
				@include mixins.font_size_def;
				color: variables.$textColorBlack;
				vertical-align: top;
			}

			.val {
				font-weight: variables.$fontWeightBold;
			}
		}

		.payment_info_box {
			@include mixins.justify_width(center);
			
			.right_box {
				flex-shrink: 0;
				margin-left: auto;
				padding-left: 20px;
				@include mixins.font_size_def;
				color: variables.$textColorBlack;
				box-sizing: border-box;
			}
		}

		& ~ .payment_info_list {
			margin-top: 20px;
		}

		& ~ .valid_check {
			margin-top: 4px;
		}
	}

	.payment_info_list {
		display: block;

		.payment_info_item {
			display: block;

			& ~ .payment_info_item {
				margin-top: 6px;
			}

			.label {
				display: inline-block;
				vertical-align: top;
				@include mixins.font_size_xxs;
				color: variables.$textColorGray;
			}

			.price {
				display: inline-block;
				font-size: 0;
				vertical-align: top;

				.val, .unit {
					@include mixins.font_size_xxs;
					color: variables.$textColorGray;
					vertical-align: top;
				}

				.val {
					font-weight: variables.$fontWeightBold;
				}
			}

			.payment_method {
				display: inline-block;
				vertical-align: top;
				@include mixins.font_size_xs;
				color: variables.$textColorBlack;
				font-weight: variables.$fontWeightMedium;
			}

			// 하위 상세 리스트
			.info_detail_list {
				display: block;
				padding: 6px 0;

				.info_detail_item {
					@include mixins.justify_width(center);
					position: relative;
					padding-left: 12px;

					& ~ .info_detail_item {
						margin-top: 6px;
					}

					&:before {
						content: '';
						display: block;
						position: absolute;
						left: 0;
						top: 5px;
						width: 9px;
						height: 9px;
						background: {
							repeat: no-repeat;
						};
						@include mixins.ratio_images('../images/bul_branch_dash', 'png', 9px, 9px);
					}

					.right_box {
						flex-shrink: 0;
						margin-left: auto;
						padding-left: 20px;
					}
				}
			}

			.payment_info_box {
				@include mixins.justify_width(center);

				.right_box {
					flex-shrink: 0;
					margin-left: auto;
					padding-left: 20px;
					@include mixins.font_size_xxs;
					color: variables.$textColorGray;

					.badge_sm {
						margin-right: 6px;
					}
				}

				& + .bul_item_asterisk {
					margin-top: 4px;
				}
			}

			.payment_detail_box {
				display: none;
			}

			&.expanded {
				.payment_info_box {
					.btn_fold_payments {
						@include mixins.transform(rotate(180deg));
					}
				}

				.payment_detail_box {
					display: block;
				}
			}
		}
	}

	.btn_fold_payments {
		display: inline-block;
		width: 19px;
		height: 19px;
		background: {
			repeat: no-repeat;
		};
		@include mixins.ratio_images('../images/btn_fold_payments', 'png', 19px, 19px);
		vertical-align: top;
	}

	.payment_util_box {
		display: block;
		margin-top: 20px;
		text-align: right;
		font-size: 0;

		.btn_xxs {
			& ~ .btn_xxs {
				margin-left: 6px;
			}
		}
	}

	.pickup_store_area {
		@include mixins.justify_width(center);

		.pickup_info_text {
			& ~ .btn_xs {
				margin-left: 12px;
			}
		}
	}

	& + .bul_list {
		margin-top: 16px;
	}

	+ .type_myroom{
		margin-top: 70px;
	}
}

/* 주문/배송 목록 > 주문 요약 정보 */
.order_history_group {
	@include mixins.justify_width;
	align-items: stretch;
	padding: 20px 0;
	background-color: #fff;
	border: 1px solid variables.$colorGray04;
	border-radius: 16px;
	box-sizing: border-box;

	.order_history_box {
		display: block;
		position: relative;
		flex: 1 0;
		padding: 0 30px;
		box-sizing: border-box;

		.history_val {
			display: block;
			@include mixins.font_size_md;
			color: variables.$textColorBlack;
			font-weight: variables.$fontWeightBold;
		}

		.history_desc {
			display: block;
			margin-top: 2px;
			@include mixins.font_size_xxs;
			color: variables.$textColorLightGray;
		}

		.btn_filter_history {
			display: inline-block;
			text-align: left;
			vertical-align: top;
		}

		& ~ .order_history_box {
			&:before {
				content: '';
				display: block;
				position: absolute;
				left: 0;
				top: 50%;
				width: 1px;
				height: 44px;
				background-color: variables.$colorGray04;
				@include mixins.transform(translateY(-50%));
			}
		}

		&.type_title {
			width: 203px;
			flex: 0 auto;

			.history_val {
				margin-top: 5px;
				@include mixins.font_size_def;
			}

			.history_desc {
				margin-top: 4px;
				@include mixins.font_size_min;

				.btn_info_popup {
					margin-left: 3px;
				}
			}
		}
	}

	+ .tbl_prod_wrap {
		margin-top: 30px;
	}
}

/* STEP - 절차 리스트 */
.step_process_list {
	display: block;
	margin-top: -12px;
	text-align: center;
	font-size: 0;

	.step_item {
		display: inline-block;
		position: relative;
		text-align: center;
		vertical-align: top;
		box-sizing: border-box;
		margin-top: 12px;
		padding-right: 12px;
		word-break: break-all;

		& ~ .step_item {
			padding-left: 18px;

			&:before {
				content: '';
				display: block;
				position: absolute;
				left: 0;
				top: 5px;
				width: 6px;
				height: 10px;
				background: {
					repeat: no-repeat;
				};
				@include mixins.ratio_images('../images/arw_step_process', 'png', 6px, 10px);
			}
		}

		&:last-child {
			padding-right: 0;
		}

		.step_num {
			display: inline-block;
			width: 20px;
			height: 20px;
			line-height: 20px;
			padding: 0 6px;
			background-color: rgba(variables.$kyoboPurple, 0.1);
			border-radius: 20px;
			font-size: variables.$fontSizeXxs;
			letter-spacing: variables.$letterSpacing;
			color: variables.$textColorPurple;
			font-weight: variables.$fontWeightBold;
			text-align: center;
			box-sizing: border-box;
			vertical-align: top;
		}
		.step_text {
			display: block;
			margin-top: 7px;
			@include mixins.font_size_xxs;
			vertical-align: top;
		}

		&.active {

		}
	}

	&.justify {
		@include mixins.justify_width(start, center);

		.step_item {
			flex: 1 0;
		}
	}
}
.step_process_box {
	display: block;
	padding: 22px 10px;
	border: 1px solid variables.$colorGray04;
	border-radius: 10px;
	box-sizing: border-box;

	& + .info_text_box {
		margin-top: 20px;
	}
}

/* 배송조회 영역 */
.delivery_tracking_wrap {
	.delivery_status_list {
		@include mixins.justify_width;

		.status_item {
			display: inline-block;
			position: relative;
			flex: 1;
			padding-right: 10px;
			vertical-align: top;
			text-align: center;

			.ico_box {
				display: inline-block;
				width: 58px;
				height: 58px;
				border-radius: 50%;
				background-color: #fff;
				border: 1px solid #fff;
				text-align: center;
				vertical-align: top;
				box-sizing: border-box;

				.ico_deli_take, .ico_deli_move, .ico_deli_arrival,
				.ico_deli_depart, .ico_deli_complete {
					display: inline-block;
					width: 26px;
					height: 26px;
					margin-top: 15px;
					background: {
						repeat: no-repeat;
						position: center;
					}
				}
				.ico_deli_take {
					@include mixins.ratio_images('../images/ico_deli_take', 'png', 26px, 26px);
				}
				.ico_deli_move {
					@include mixins.ratio_images('../images/ico_deli_move', 'png', 26px, 26px);
				}
				.ico_deli_arrival {
					@include mixins.ratio_images('../images/ico_deli_arrival', 'png', 26px, 26px);
				}
				.ico_deli_depart {
					@include mixins.ratio_images('../images/ico_deli_depart', 'png', 26px, 26px);
				}
				.ico_deli_complete {
					@include mixins.ratio_images('../images/ico_deli_complete', 'png', 26px, 26px);
				}
			}

			.info_box {
				display: block;
				margin-top: 10px;

				.desc {
					display: block;
					@include mixins.font_size_xxs;
					color: variables.$textColorLightGray;
				}
			}

			&:after {
				content: '';
				display: block;
				position: absolute;
				right: 2px;
				top: 24px;
				width: 5px;
				height: 10px;
				background: {
					repeat: no-repeat;
					position: center;
				}
				@include mixins.ratio_images('../images/arw_delivery_step', 'png', 5px, 10px);
			}

			& ~ .process_item {
				padding-left: 10px;
			}
			&:last-child {
				padding-right: 0;

				&:after {
					display: none;
				}
			}

			// 완료된 단계
			&.complete {
				.ico_box {
					.ico_deli_take {
						@include mixins.ratio_images('../images/ico_deli_take_active', 'png', 26px, 26px);
					}
					.ico_deli_move {
						@include mixins.ratio_images('../images/ico_deli_move_active', 'png', 26px, 26px);
					}
					.ico_deli_arrival {
						@include mixins.ratio_images('../images/ico_deli_arrival_active', 'png', 26px, 26px);
					}
					.ico_deli_depart {
						@include mixins.ratio_images('../images/ico_deli_depart_active', 'png', 26px, 26px);
					}
					.ico_deli_complete {
						@include mixins.ratio_images('../images/ico_deli_complete_active', 'png', 26px, 26px);
					}
				}
			}

