					padding-left: 19px;
					padding-bottom: 16px;
					font-size: 0;

					&:before {
						content: '';
						display: inline-block;
						position: absolute;
						left: 1px;
						top: 7px;
						width: 5px;
						height: 5px;
						border-radius: 50%;
						background-repeat: no-repeat;
						background-color: variables.$colorGray02;
					}

					&:after {
						content: '';
						display: inline-block;
						position: absolute;
						left: 3px;
						top: 16px;
						bottom: -3px;
						width: 1px;
						border-left: 1px dashed variables.$colorGray04;
					}

					&:last-child {
						padding-bottom: 0;

						&:after {
							bottom: -11px;
						}
					}

					.status_time {
						display: inline-block;
						flex-shrink: 0;
						vertical-align: top;
						font-size: 0;

						.time {
							display: inline-block;
							@include mixins.font_size_xs;
							color: variables.$textColorLightGray;
							vertical-align: top;
						}
					}

					.status_location {
						display: inline-block;
						margin-left: 20px;
						vertical-align: top;
						font-size: 0;

						.label {
							display: inline-block;
							@include mixins.font_size_xs;
							color: variables.$textColorBlack;
							vertical-align: top;
						}
						.gap {
							display: inline-block;
							font-size: 0;
							width: 1px;
							height: 10px;
							margin: 5px 10px 0 10px;
							background-color: variables.$colorGray02;
							vertical-align: top;
							box-sizing: border-box;
						}
						.status {
							display: inline-block;
							@include mixins.font_size_xs;
							color: variables.$textColorBlack;
							vertical-align: top;
						}
					}

					&.active {
						&:before {
							left: 0;
							width: 7px;
							height: 7px;
							border-radius: 0;
							background-color: transparent;
							@include mixins.ratio_images('../images/bg_delivery_tracking_mark', 'png', 7px, 7px);
						}

						&:after {
							top: 17px;
						}

						.status_location {
							.status {
								color: variables.$textColorPurple;
								font-weight: variables.$fontWeightBold;
							}
						}
					}
				}
			}

			& + .tracking_item {
				padding-top: 19px;
			}

			&:last-child {
				padding-bottom: 0;

				.tracking_info_list {
					.info_item {
						&:last-child:after {
							display: none;
						}
					}
				}
			}
		}
	}

	.bul_item_asterisk {
