								background-color: variables.$kyoboPurple;
							}
						}

						.progress_info{
							@include mixins.justify_width(center, between);
							margin-top: 8px;

							.percent{
								@include mixins.font_size_xs;
								font-weight: variables.$fontWeightMedium;
								color: variables.$kyoboPurple
							}

							.state{
								.val, .total{
									@include mixins.font_size_min;
									color: variables.$colorGray01;
								}

								.val{
									padding-right: 3px;
									color: variables.$textColorBlack;
								}
							}
						}
					}

					&.no_data{
						display: block;
						padding: 47px 40px 30px;
						box-sizing: border-box;

						.no_data_desc{
							@include mixins.font_size_xs;
							font-weight: variables.$fontWeightMedium;
							text-align: center;
							color: variables.$textColorBlack;

							&:before{
								content: none;
							}
						}

						.no_data_sdesc{
							width: 100%;
							margin-top: 5px;
							margin-bottom: 34px;
							padding-bottom: 33px;
							border-bottom: 1px solid variables.$colorGray04;
							@include mixins.font_size_xxs;
							color: variables.$textColorLightGray;
							text-align: center;
						}

						.prod_area{
							align-items: flex-start;

							.prod_info_box{
								.btn_xs{
									margin-top: 12px;
								}
							}
						}

					}
				}
			}
		}
	}

	.activity_more_box{
		display: inline-block;
		width: calc(100% - 311px - 25px);
		margin-left: 25px;
		vertical-align: top;

		.my_quick_link_box{
			.title_wrap{
				.btn_link_control{
					display: none;
				}
			}

			.my_quick_list_wrap{
				padding: 27px 26px 27px 25px;
				@include mixins.justify_width;
				border: 1px solid variables.$colorGray04;
				border-radius: 16px;

				.my_quick_list{
					@include mixins.justify_width;

					.my_quick_item{
						width: 92px;
						box-sizing: border-box;

						~ .my_quick_item{
							margin-left: 9px
						}
					}

					.my_quick_link{
						display: block;
						height: 100%;

						span[class^=ico_]{
							position: relative;
							display: block;
							width: 62px;
							height: 62px;
							margin: 0 auto;
							background-color: variables.$colorGray06;
							border-radius: 100%;

							&:after{
								content: '';
								position: absolute;
								display: block;
								width: 27px;
								height: 27px;
								top: 18px;
								left: 19px;
							}

							&.ico_my_receipt{
								&:after{
									@include mixins.ratio_images('../images/ico_my_receipt', 'png', 27px, 27px);
								}
							}

							&.ico_my_chart{
								&:after{
									@include mixins.ratio_images('../images/ico_my_chart', 'png', 27px, 27px);
								}
							}

							&.ico_my_speech{
								&:after{
									@include mixins.ratio_images('../images/ico_my_speech', 'png', 27px, 27px);
								}
							}

							&.ico_my_calendar{
								&:after{
									@include mixins.ratio_images('../images/ico_my_calendar', 'png', 27px, 27px);
								}
							}

							&.ico_my_point{
								&:after{
									@include mixins.ratio_images('../images/ico_my_point', 'png', 27px, 27px);
								}
							}
						}

						.text{
							display: block;
							margin-top: 14px;
							@include mixins.font_size_xxs;
							text-align: center;
							@include mixins.ellipsis;
						}
					}
				}

				.btn_my_quick_add{
					display: block;
					width: 92px;
					margin-left: 9px;
					box-sizing: border-box;

					.ico_quick_add{
						position: relative;
						display: block;
						width: 62px;
						height: 62px;
						margin: 0 auto;
						background-color: variables.$colorGray06;
						border-radius: 100%;


						border: 1px dashed variables.$colorGray04;
						@include mixins.ratio_images('../images/ico_my_quick_add', 'png', 12px, 12px);
						background: {
							repeat: no-repeat;
							position: center;
						};
						box-sizing: border-box;
					}

					.text{
						display: block;
						margin-top: 14px;
						@include mixins.font_size_xxs;
						text-align: center;
						@include mixins.ellipsis;
						color: variables.$textColorLightGray;
					}
				}
			}

			.no_data_line{
				height: 150px;
			}

			&.active{
				.title_wrap{
					.btn_change{
						display: none;
					}

					.btn_link_control{
						display: block;

						.text{
							@include mixins.font_size_xxs;
							font-weight: variables.$fontWeightMedium;
							color: variables.$textColorLightGray;

						}

						&.save{
							.text {
								color: variables.$textColorBlack;
							}
						}

						~ .btn_link_control{
							margin-left: 0;

							&:before{
								content: '';
								display: inline-block;
								width: 1px;
								height: 10px;
								margin: 4px 8px 0;
								background-color: variables.$colorGray04;
								vertical-align: top;
							}
						}
					}
				}

				.my_quick_list_wrap{
					.my_quick_list{
						.my_quick_item{
							.my_quick_link{
								pointer-events: none;
							}
						}
					}
				}
			}
		}

		.sam_voucher_wrap{
			@include mixins.justify_width;
			margin-top: 10px;

			.sam_logo{
				display: inline-block;
				width: 44px;
				height: 13px;
				margin-top: 4px;
				vertical-align: top;

				img{
					width: 100%;
					vertical-align: top;
				}
			}

			.sam_info_link{
				@include mixins.justify_width(start, start, column);
				width: 100%;
				height: 100%;
			}

			.sam_info_box, .induce_box{
				flex: 1;
				padding: 22px 24px 24px 20px;
				border-radius: 16px;
				height: 150px;
				box-sizing: border-box;

				.info_desc{
					@include mixins.font_size_sm;

				}
			}

			.sam_info_box{
				background-color: rgba(#06BBC0, .07);
			}

			.induce_box{
				margin-left: 10px;
				background-color: rgba(variables.$kyoboPurple, .07);

				.count_val{
					margin-top: auto;
					align-self: flex-end;

					.val{
						@include mixins.font_size_def;
						font-weight: variables.$fontWeightBold;
					}

					.unit{
						@include mixins.font_size_xs;
					}
				}
			}

			.btn_more_view{
				margin-top: auto;
				align-self: flex-end;
			}

		}


	}
}

.no_data_line{
	width: 100%;
	height: 100%;
	@include mixins.justify_width(center, center, column);
	border-radius: 10px;
	border: 1px solid variables.$colorGray04;
	box-sizing: border-box;

	.no_data_desc{
		@include mixins.font_size_sm;
		color: variables.$textColorLightGray;
	}

	.btn_add_cont{
		margin-top: 16px;

		.ico_add_cont{
			width: 20px;
			height: 20px;
			@include mixins.ratio_images('../images/btn_add_cont', 'png', 20px, 20px);
		}

		.text{
			margin-left: 6px;
			@include mixins.font_size_xxs;
			font-weight: variables.$fontWeightMedium;
			color: variables.$textColorGray;
		}
	}
