.prod_filter_wrap{
	position: relative;

	.filter_area{
		display: none;
		position: absolute;
		top: 54px;
		right: 0;
		width: 510px;
		border: 1px solid variables.$textColorBlack;
		border-radius: 12px;
		background-color: #fff;
		padding: 20px 29px 29px;
		z-index: 2;
		box-sizing: border-box;

		.filter_header{
			@include mixins.justify_width(center);

			.filter_title{
				@include mixins.font_size_md;
				font-weight: variables.$fontWeightMedium;
			}

			.btn_close_filter{
				width: 24px;
				height: 24px;
				margin-left: auto;

				.ico_filter_close {
					position: relative;
					z-index: 3;
					display: block;
					width: 24px;
					height: 24px;
					background: {
						position: center;
						repeat: no-repeat;
					}
					@include mixins.ratio_images('../images/btn_dialog_close', 'png', 24px, 24px);
				}
			}
		}

		.filter_contents{
			margin-top: 20px;

			.info_text_box{
				margin-bottom: 20px;
			}
		}

		.filter_bottom{
			margin-top: 30px;
			text-align: center;
			font-size: 0;

			.btn_ip{
				min-width: 140px;
			}
		}
	}

	&.active{
		.filter_area{
			display: block;
		}
	}
}

.claim_count_box {
	display: none;

